import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import "../css/style.css"

export default function Clean1() {
    const data = useStaticQuery(graphql`
query Clean1 {
      span: file(relativePath: {eq: "span.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
  }`
)

    return (
        <section id="actividades" className="clean1-area">
            <div className="div-tittle-mision">
                <Img className="span" alt="" fluid={data.span.childImageSharp.fluid} />
                <br/>
                <p>Actividades a desarrollar de un operario de limpieza</p>
            </div>
        </section>
    )
}