import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import "../css/style.css"

export default function Clean3() {
    const data = useStaticQuery(graphql`
query Clean3 {
      clean3: file(relativePath: {eq: "limpieza.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
      span: file(relativePath: {eq: "span.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
  }`
)

    return (
        <section id="actividades" className="mision-area grid-container-clean">
            <div className="div-text-clean2"><br/>
            <Img className="span" alt="" fluid={data.span.childImageSharp.fluid} /><br/><br/>
            <p><b>Limpieza general de áreas comunes y oficinas:</b>  Realizar las labores de limpieza y aseo en general, en forma eficiente y 
            oportuna a todas las áreas, mobiliario y equipo que requieran de ello, mediante el cual eliminamos la 
            suciedad visible de una superficie, realizar las tareas de limpieza en perfecto estado en cuanto a 
            limpieza y orden se refiere.</p>
            <p><b>Sanitización y desinfección:</b>  Efecto residual, permite que su acción bactericida persista por un largo 
            periodo, potente acción germicida con producto biodegradable, amplio espectro de microorganismos que elimina 
            bacterias, hongos, virus y algas, efectivo para pisos, baños, inodoros, pasamanos, manijas, teléfonos.</p>
            <p><b>A.</b>  Desinfectar todo tipo de superficies que puedan estar expuestas al uso 
            diario o no.</p>
            <p><b>B.</b>  Desempolvar y limpiar el equipo de oficina o del área de trabajo.</p>
            </div>
            <div>
                <Img className="clean2" alt="" fluid={data.clean3.childImageSharp.fluid} />
            </div>
            <br/><br/>
        </section>
    )
}