import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import "../css/style.css"

export default function Clean2() {
    const data = useStaticQuery(graphql`
query Clean2 {
      clean2: file(relativePath: {eq: "sanidad.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
      span: file(relativePath: {eq: "span.png"}) {
        childImageSharp {
          fluid {
              src
              srcWebp
              aspectRatio
              base64
          }
        }
      }
  }`
)

    return (
        <section id="actividades" className="mision-area grid-container-clean">
            <div className="div-text-clean2"><br/>
            <Img className="span" alt="" fluid={data.span.childImageSharp.fluid} /><br/><br/>
            <p><b>Limpieza de suelos:</b>  realizando actividades tales como barrer, fregar y pasar la mopa.</p>
            <p><b>Limpieza de muebles:</b>  desengrasado, limpieza de polvo por cada parte del mueble.</p>
            <p><b>Limpieza de cristales:</b>  quitando el polvo y suciedad, así como gotas de lluvias, que pueda 
            haber en ellos con productos especializados en cristales.</p>
            <p><b>Vaciado de papeleras:</b>  siempre llevando a cabo tareas de reciclajes pertinentes.</p>
            <p><b>Reposición de materiales:</b>  aquellos necesarios para el lugar en el cual se trabaje. 
            Aunque casi siempre, suelen ser los mismos: servilletas, papel higiénico, jabón de manos.</p>
            </div>
            <div>
                <Img className="clean2" alt="" fluid={data.clean2.childImageSharp.fluid} />
            </div>
            <br/><br/><br/><br/>
        </section>
    )
}